.navbar {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background-color: #141414;
    color: white;
  }
  
  .navbar .logo {
    font-size: 24px;
    text-decoration: none;
    color: white;
  }
  
  .nav-links a {
    color: white;
    text-decoration: none;
    margin: 0 10px;
  }
  
  .nav-links a:hover {
    color: blue;
  }